import { itembank } from '@api';
const { viewSuitById } = itembank;
export default {
  data() {
    return {
      interInfo: {}
    };
  },
  methods: {},
  async onLoad(e) {
    const data = await viewSuitById({ id: e.id});
    this.interInfo = data;
    this.$store.state.viewTitle = data.title
    console.info(data);
  }
};
